// src/app/config/page.tsx
"use client";

import AuthGuard from "@/components/AuthGuard";
import AppShell from "@/components/shell/AppShell";
import { useToast } from "@/components/Toast";
import { supabaseBrowser } from "@/lib/supabase/client";
import { applyTheme, ThemeMode } from "@/lib/theme";
import { useEffect, useMemo, useState } from "react";
import { motion } from "framer-motion";
import {
  User,
  SlidersHorizontal,
  Check,
  Loader2,
  ShieldAlert,
  Trash2,
  Save,
} from "lucide-react";
import { useRouter } from "next/navigation";

/** Apenas o que editamos aqui: nome + tema */
type Profile = {
  full_name: string | null;
  theme: ThemeMode | null;
};

const THEMES: { value: ThemeMode; label: string }[] = [
  { value: "system", label: "Sistema" },
  // { value: "light", label: "Claro" }, // reative se quiser voltar o tema claro
  { value: "dark", label: "Escuro" },
];

export default function ConfigPage() {
  return (
    <AuthGuard>
      <AppShell>
        <Content />
      </AppShell>
    </AuthGuard>
  );
}

function Content() {
  const supabase = useMemo(() => supabaseBrowser(), []);
  const toast = useToast();
  const router = useRouter();

  const [email, setEmail] = useState<string>("");
  const [profile, setProfile] = useState<Profile>({ full_name: "", theme: "system" });

  const [loading, setLoading] = useState(true);
  const [msg, setMsg] = useState<string | null>(null);

  // estados visuais de salvar
  const [saving, setSaving] = useState(false);
  const [saveOkTick, setSaveOkTick] = useState(0);

  // Danger Zone
  const [confirmText, setConfirmText] = useState("");
  const [deleting, setDeleting] = useState(false);

  useEffect(() => {
    (async () => {
      setLoading(true);
      setMsg(null);

      const { data: udata } = await supabase.auth.getUser();
      const user = udata.user;
      if (!user) {
        setLoading(false);
        return;
      }
      setEmail(user.email ?? "");

      const { data: p, error } = await supabase
        .from("profiles")
        .select("full_name, theme")
        .eq("id", user.id)
        .single();

      if (error) {
        setMsg(error.message);
      }

      const next: Profile = {
        full_name: p?.full_name ?? "",
        theme: (p?.theme as ThemeMode) ?? "system",
      };

      setProfile(next);
      applyTheme(next.theme || "system");
      setLoading(false);
    })();
  }, [supabase]);

  function handleThemeChange(newTheme: ThemeMode) {
    setProfile((p) => ({ ...p, theme: newTheme }));
    applyTheme(newTheme);
    try {
      localStorage.setItem("theme", String(newTheme));
      window.dispatchEvent(new Event("theme-change"));
    } catch {}
  }

  async function save() {
    setSaving(true);
    setMsg(null);
    try {
      const { data: udata } = await supabase.auth.getUser();
      const uid = udata.user?.id;
      if (!uid) throw new Error("Sessão inválida.");

      const payload = {
        full_name: (profile.full_name ?? "").trim() || null,
        theme: profile.theme ?? "system",
      };

      const { error } = await supabase.from("profiles").update(payload).eq("id", uid);
      if (error) throw error;

      applyTheme(payload.theme as ThemeMode);
      try {
        localStorage.setItem("theme", String(payload.theme));
        window.dispatchEvent(new Event("theme-change"));
      } catch {}

      toast.push("Configurações salvas.");
      // aciona microanimação no header
      setSaveOkTick((t) => t + 1);
    } catch (e: any) {
      setMsg(e.message ?? "Não foi possível salvar.");
      toast.push("Erro ao salvar");
    } finally {
      setSaving(false);
    }
  }

  async function deleteAccount() {
    if (confirmText !== "DELETAR") {
      toast.push('Digite "DELETAR" para confirmar');
      return;
    }
    setDeleting(true);
    try {
      const { data: sessionData } = await supabase.auth.getSession();
      const token = sessionData.session?.access_token;
      if (!token) throw new Error("Sessão inválida.");

      const res = await fetch("/api/delete-account", {
        method: "POST",
        headers: { Authorization: `Bearer ${token}` },
      });
      if (!res.ok) {
        const j = await res.json().catch(() => ({}));
        throw new Error(j?.error || `HTTP ${res.status}`);
      }

      toast.push("Conta excluída.");
      await supabase.auth.signOut();
      router.replace("/login");
    } catch (e: any) {
      toast.push(e.message ?? "Falha ao excluir conta.");
    } finally {
      setDeleting(false);
    }
  }

  const themeBadge =
    (profile.theme ?? "system") !== "system" ? (
      <span className="ml-2 inline-flex items-center rounded-full border border-white/10 bg-[var(--accent)]/15 px-2 py-0.5 text-xs text-[var(--accent)]">
        Ativo
      </span>
    ) : null;

  return (
    <motion.main
      initial={{ opacity: 0, y: 10 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.35, ease: "easeOut" }}
      className="max-w-3xl mx-auto px-5 md:px-8 py-8 md:py-10"
    >
      {/* Header com estética do /me */}
      <div className="mb-6">
        <div className="flex items-center gap-2">
          <h1 className="text-2xl font-semibold text-lime-400/90 tracking-tight">
            Configurações
          </h1>
          <motion.span
            key={`ok-${saveOkTick}`}
            initial={{ opacity: 0, y: -4, filter: "blur(2px)" }}
            animate={{ opacity: 1, y: 0, filter: "blur(0px)" }}
            exit={{ opacity: 0, y: -4 }}
            transition={{ duration: 0.25 }}
            className="text-lime-300/90 text-sm"
          >
            {saveOkTick > 0 && "• salvo"}
          </motion.span>
        </div>
        <p className="text-white/70 mt-1">Personalize seu perfil e preferências.</p>
      </div>

      {msg && (
        <div className="mt-4 p-3 rounded-xl border border-white/10 bg-white/10 text-sm">
          {msg}
        </div>
      )}

      {/* Card: Perfil */}
      <section
        className="bg-[var(--surface)]/80 backdrop-blur-md border border-white/10
                   rounded-2xl shadow-xl p-6 mb-6"
      >
        <h2 className="text-lg font-semibold text-lime-400/90 flex items-center gap-2">
          <User size={16} className="opacity-80" />
          Perfil
        </h2>

        {loading ? (
          <div className="mt-4 text-sm opacity-70 inline-flex items-center gap-2">
            <Loader2 size={16} className="animate-spin" />
            Carregando…
          </div>
        ) : (
          <div className="mt-4 grid gap-4">
            {/* E-mail (somente leitura) */}
            <div>
              <label className="block text-sm opacity-70 mb-1">E-mail</label>
              <input
                value={email}
                disabled
                className="w-full h-10 rounded-xl bg-white/5 border border-white/10
                           text-sm px-3 outline-none cursor-not-allowed opacity-70"
              />
            </div>

            {/* Nome de exibição */}
            <div>
              <label className="block text-sm opacity-70 mb-1">Nome de exibição</label>
              <input
                value={profile.full_name ?? ""}
                onChange={(e) =>
                  setProfile((p) => ({ ...p, full_name: e.target.value }))
                }
                placeholder="Como você quer aparecer"
                className="w-full h-10 rounded-xl bg-white/5 border border-white/10
                           focus:border-lime-400 focus:ring-2 focus:ring-lime-400/25
                           text-sm px-3 outline-none transition"
              />
            </div>
          </div>
        )}
      </section>

      {/* Card: Preferências */}
      <section
        className="bg-[var(--surface)]/80 backdrop-blur-md border border-white/10
                   rounded-2xl shadow-xl p-6 mb-6"
      >
        <h2 className="text-lg font-semibold text-lime-400/90 flex items-center gap-2">
          <SlidersHorizontal size={16} className="opacity-80" />
          Preferências
        </h2>

        {loading ? (
          <div className="mt-4 text-sm opacity-70 inline-flex items-center gap-2">
            <Loader2 size={16} className="animate-spin" />
            Carregando…
          </div>
        ) : (
          <div className="mt-4 grid gap-4 sm:grid-cols-2">
            <div>
              <label className="block text-sm mb-1">
                Tema {themeBadge}
              </label>
              <select
                value={profile.theme ?? "system"}
                onChange={(e) => handleThemeChange(e.target.value as ThemeMode)}
                className="w-full h-10 rounded-xl bg-white/5 border border-white/10
                           focus:border-lime-400 focus:ring-2 focus:ring-lime-400/25
                           text-sm px-3 outline-none transition"
              >
                {THEMES.map((t) => (
                  <option key={t.value} value={t.value}>
                    {t.label}
                  </option>
                ))}
              </select>
              <p className="mt-1 text-xs opacity-70">
                “Sistema” segue o tema do seu dispositivo. (Tema claro está desativado neste app.)
              </p>
            </div>
            <div className="hidden sm:block" />
          </div>
        )}

        {/* Botão salvar */}
        <div className="mt-6 flex justify-end">
          <button
            onClick={save}
            disabled={saving || loading}
            className="inline-flex items-center gap-2 px-5 h-10 rounded-xl
                       bg-gradient-to-r from-lime-500/85 to-lime-400/85
                       hover:from-lime-400 hover:to-lime-300 text-black
                       font-medium transition shadow-[0_0_15px_-3px_rgba(163,230,53,0.45)]
                       disabled:opacity-60"
          >
            {saving ? <Loader2 size={16} className="animate-spin" /> : <Save size={16} />}
            {saving ? "Salvando…" : "Salvar alterações"}
          </button>
        </div>
      </section>

      {/* Zona de perigo */}
      <section
        className="bg-red-950/60 border border-red-700/30 text-red-300
                   rounded-2xl p-5 backdrop-blur-sm shadow-inner"
      >
        <div className="flex items-center gap-2 text-red-300">
          <ShieldAlert size={18} />
          <h3 className="font-semibold">Zona de perigo</h3>
        </div>
        <p className="text-sm mt-1">
          Excluir sua conta é <span className="font-semibold">irreversível</span>. Todos os seus
          dados pessoais poderão ser removidos.
        </p>

        <div className="mt-3 grid gap-3 md:grid-cols-[1fr_auto] items-center">
          <input
            value={confirmText}
            onChange={(e) => setConfirmText(e.target.value)}
            placeholder='Digite "DELETAR" para confirmar'
            className="h-10 rounded-xl bg-white/5 border border-white/10
                       focus:border-red-400 focus:ring-2 focus:ring-red-400/25
                       px-3 outline-none text-sm text-red-100 placeholder:text-red-200/60"
          />
          <button
            onClick={deleteAccount}
            disabled={confirmText !== "DELETAR" || deleting}
            className="h-10 px-4 rounded-xl bg-red-600/85 hover:bg-red-500
                       text-white font-medium inline-flex items-center gap-2
                       disabled:opacity-60 transition justify-center"
            title="Excluir definitivamente sua conta"
          >
            {deleting ? (
              <>
                <Loader2 size={16} className="animate-spin" /> Excluindo…
              </>
            ) : (
              <>
                <Trash2 size={16} /> Excluir conta
              </>
            )}
          </button>
        </div>
      </section>
    </motion.main>
  );
}
